@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.eot?od5efa');
  src: url('../fonts/icomoon.eot?od5efa#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?od5efa') format('truetype'),
    url('../fonts/icomoon.woff?od5efa') format('woff'),
    url('../fonts/icomoon.svg?od5efa#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

